.page {
  color: #2f4f4f;
  font-family: Arial, sans-serif;
  font-size: 20px;
  line-height: 23px;
}

.page__body {
  min-width: 500px;
  margin: 0;
}

.page__people {
  --column: 2;
  grid-template-columns: repeat(var(--column), 1fr);
  justify-content: center;
  gap: 40px 30px;
  width: 430px;
  margin: 0 auto;
  padding-block: 30px;
  display: grid;
}

@media (width >= 1000px) {
  .page__people {
    --column: 4;
  }
}

.person {
  flex-direction: column;
  align-items: center;
  width: fit-content;
  display: flex;
  position: relative;
}

.person__photo-container {
  position: relative;
}

.person__photo {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  transition: all 1s;
  display: block;
}

.person:hover .person__photo {
  border-radius: 60px;
  width: 240px;
  height: 240px;
  position: relative;
}

.person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  display: flex;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.person__info {
  z-index: 2;
  text-align: center;
  padding-top: 8px;
  width: 100%;
  padding-inline: 35px;
}

.person__name {
  font-size: inherit;
  line-height: inherit;
  color: inherit;
}

.person__extra {
  color: #000;
  display: none;
  position: absolute;
}

.person:after {
  content: "";
  z-index: 1;
  background-color: #eee;
  width: 220px;
  height: 287px;
  transition: all 1s;
  display: none;
  position: absolute;
  top: -10px;
}

.person:hover:after {
  content: "";
  z-index: 1;
  background-color: #eee;
  width: 264px;
  height: 344px;
  display: block;
  position: absolute;
  top: -10px;
}

.person:hover .person__extra {
  box-sizing: border-box;
  justify-content: space-between;
  width: 100%;
  margin-top: 9px;
  padding-inline: 35px;
  display: flex;
}

.person:hover .person__photo-container {
  z-index: 2;
}

.person__sport:after {
  content: ", ";
}

.person__pay:before {
  content: "$";
}

.person__pay:after {
  content: "M";
}
